@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@300;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
	background: #1a1a1a;
	margin: 0;
	padding: 0;
	font-family: 'Josefin Sans', sans-serif;
}

.card {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%,-50%);
	width: 330px;
	height: 440px;
	transform-style: preserve-3d;
	perspective: 600px;
	transition: .8s;
}
.card .back .details {
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	left: 0;
	width: 100%;
	text-align: center;
	padding: 40px;
	box-sizing: border-box;
}
.card .back .details h2 {
	margin: 0;
	padding: 0;
	font-size: 20px;
	color: #fff;
}
.card .back .details h2 span {
	color: #C49843;
	font-size: 16px;
}
.social {
	padding: 10px 0;
}
.social a {
	display: inline-block;
	width: 36px;
	height: 36px;
	text-align: center;
	background: #41392B;
	color: #fff;
	text-decoration: none;
	border-radius: 0%;
	transition: .3s;
}
.social a .fa {
	font-size: 16px;
	line-height: 36px;
}
.social a .fab {
	font-size: 16px;
	line-height: 36px;
}
.tech .fab {
	padding: 10px;
	color: #a0a0a0;
}
p {
	color: #C49843;
	font-size: 14px;
}
.link .route {
	color: #A1A1A1;
	font-size: 12px;
	text-decoration: none;
}

.footer {
	position: fixed;
	left: 0;
	bottom: 50px;
	width: 100%;
	color: #707070;
	font-size: x-small;
	text-align: center;
}

.card .front {	overflow:hidden;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: #0a0a0a;
	-webkit-backface-visibility: hidden;
	        backface-visibility: hidden;
	transform: rotateY(0deg);
	transition: .8s;
}
.card:hover .front {
	transform: rotateY(-180deg);
}
.card .back {
	overflow:hidden;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: #0a0a0a;
	-webkit-backface-visibility: hidden;
	        backface-visibility: hidden;
	transform: rotateY(180deg);
	transition: .8s;
}
.card .back:before {
	content: ' ';
	position: absolute;
	top: 0;
	left: -50%;
	width: 100%;
	height: 100%;
	background: rgba(255,255,255,.01);
}
.card:hover .back {
	transform: rotateY(0deg);
}
.social a:hover {
	color: #0a0a0a;
	background: #DBAA4B;
}
.link {
	color: #A1A1A1;
	text-align: center;
	position: absolute;
	bottom: 0;
	left: 50%;
	transform: translate(-50%, 100%);
}
.link .route:hover {
	color: #eee;
	text-decoration: underline;
}

